@charset "utf-8";

@import "../mixin";

/* -----------------------------------------------
 [0. Normalize(ノーマライズ)]
-------------------------------------------------- */
/* Base
--------------------------------------------------*/
html {
	font-family: sans-serif; /* 1 */
	-ms-text-size-adjust: 100%; /* 2 */
	-webkit-text-size-adjust: 100%; /* 2 */
}
html{
	&.no-edge.no-ie{
		*{
			font-feature-settings : "palt" 1;
			&.not-palt{
				font-feature-settings : initial ;
			}
		}
	}
}

// html.ie,html.edge{
